//

$breakpoints: (
    "xxxl": 1919px,
    // ‚Üì below 1920
    "xxl" : 1439px,
    // ‚Üì below 1440
    "xl" : 1299px,
    // ‚Üì below 1300
    "lg" : 1199px,
    // ‚Üì below 1200
    "md" : 991px,
    // ‚Üì below 992
    "sm" : 767px,
    // ‚Üì below 768
    "xs" : 575px // ‚Üì below 576
);



// Mixin for max-width media queries
@mixin respond($breakpoint) {

    @if map-has-key($breakpoints, $breakpoint) {
        @media (max-width: map-get($breakpoints, $breakpoint)) {
            @content;
        }
    }

    @else {
        @warn "‚ö†Ô∏è Breakpoint '#{$breakpoint}' not found.";
    }
}